import React, { Component } from 'react';
import Widecard from '../components/Widecard';

class Education extends Component {
    render() {
        return (
            <div className="condiv">
            <h1 className="subtopic">My Education</h1>
            <Widecard title="Web Developer Associate’s degree" where="Wake Technical Community College" from="2018" to="Present"/>
            <Widecard title="Bachelors of E¬ducation, Biology and Geology Department" where="Alexandria University" from="May 2004" to="June 2008"/>
            </div>
            )
        }
    }
    
export default Education
    